import styles from "./LandingPage.module.css";
import StartButton from "../../components/StartButton/StartButton";
import { Link } from "react-router-dom";


const LandingPage = () => {
    console.log("stylesland: ",styles);
    return (
        <div className={styles.divLanding}>
            <main>
                <section>
                <h1>Ceasar Chef</h1>
                    <p>Para ti amante de la cocina, has llegado al lugar indicado. <br />
                     <br />
                    En easy cook encontrarás las mejores recetas de cocina a la vez que podrás también compartir tus recetas para que mucha más gente la conozca.
                    </p>
                </section>
                <aside>
                    <StartButton  className={styles.linkLanding}> </StartButton>
                </aside>
            </main>
            <footer>
                <Link to={'/knowMe'} className={styles.CONOCEME}>CONOCEME</Link>
            </footer>
        </div>
      
    )
};


export default LandingPage;