import React from "react";
import { Link } from "react-router-dom";
import style from "./Card.module.css";


function dietsString (arr){
    arr = arr.map(el=>el.name);
     let stringDiets = arr.join('  -  ');

     return stringDiets

    
}



class Card extends React.Component {
    // constructor(props){
    //     super(props)
    // }

     myRef = React.createRef();

    addBr(e){
      let string ='';
   if(e){ string = e.innerText;
        if(string.length > 36){
            for (let i = 35; i >= 0; i--) {
                    if(string[i] === ' '){
                        let arr= string.split('')
                
                        arr.splice(i,0, '<br>')
        
                        let stringBr=arr.join('')
                        
                        e.innerHTML=stringBr;
                        
                        return 
                    }  
            }
         }else{
            e.style.cssText= 'display:flex ; justify-content:center ; align-items:center';
         }
        }
    }
        
    testRef=(e)=>{
        this.addBr(e);
    }

    render(){
        return(
            <Link to={'/details'} className={style.card}>
                    <h4 ref={this.testRef} className={style.test}>{this.props.name}</h4>
                    <img src={this.props.img} alt="Recipe" />    
                    <span>
                    <p><strong>Diets: </strong>{dietsString(this.props.diets)}</p>
                    </span>
            </Link>
        )
    }
    
};

export default Card;



