import styles from './SearchBar.module.css'
import logoLupa from '../../imgs/logos/lupa.png';


const SearchBar = ()=>{
    return(
        <div>
            <form action="" className={styles.formSearchBar}>
                <input type="text" placeholder="Busca una receta" name="name"/>
                <button type="submit"><img src={logoLupa} alt="Lupa logo" /></button>
            </form>
        </div>
    )
};

export default SearchBar

