import { GET_ALL_RECIPES } from "../actions/actions-types";

const initialState = {
     recipes : [],
};


const rootReducer = (state = initialState, action)=>{
    console.log(action);
    switch (action.type) {
        case GET_ALL_RECIPES:
            return {
                ...state,
                recipes: [...state.recipes,...action.payload]
            }
            
    
        default:
            console.log("jajajadefalut");
            return{
                ...state
            }
    }

};


export default rootReducer;