import { useState } from 'react';
import style from './CreatePage.module.css';


const CreatePage = () => {

    const [recipeToSend, setRecipeToSend]=useState({
        name:"",
        resumenDelPlato:"",
        healthScore:"",
        stepByStep:"",
        dietTypes:[],
    })

    const onChangeHanler=(event)=>{
        const target = event.target.name;
        const value = event.target.value
        console.log(recipeToSend);
        setRecipeToSend({...recipeToSend,[target]:value})
        console.log(recipeToSend);

    }

    return(
        <div id={style.divForm}>
            <form>
                <h1>Create your Recipe</h1>
                <divs>
                    <div className={style.divsSmallImputs}>
                        <label htmlFor="name" >Nombre de tu receta: </label>
                        <input  onChange={(e)=>onChangeHanler(e)} placeholder='Write your recipe Name...'  type="text" name="name" autoComplete='off'/>
                    </div>
                    <div className={style.divsSmallImputs}>
                        <label htmlFor="healthScore">¿Qué tan saludable es?</label>
                        <input onChange={(e)=>onChangeHanler(e)} placeholder='On a scale from 1 to 100...' type="number" min='0' max='100' name="healthScore" autoComplete='off'/>
                    </div>
                    <div className={style.divsSmallImputs}>
                        <label htmlFor="imgUrl">Imagen demostrativa</label>
                        <input onChange={(e)=>onChangeHanler(e)} placeholder='Enter a url of a reference image...' type="url" name="imgUrl" autoComplete='off' />
                    </div>
                    <div className={style.bigTextContainer}>
                        <div>
                            <label htmlFor="resumenDelPlato">Resumen del plato</label>
                            <textarea onChange={(e)=>onChangeHanler(e)} placeholder='Enter a quikly summary of your recipe...' type="textarea" name="resumenDelPlato" className={style.textarea}/>
                        </div>
                        <div id={style.steps}>
                            <label htmlFor="stepByStep">Step by Step</label>
                            <textarea onChange={(e)=>onChangeHanler(e)} placeholder='Describe your recipe step by step...' type="text" name="stepByStep" className={style.textarea}/>
                        </div>
                    </div>
                </divs>
                <ul id={style.dietsOptions}>
                    <div>
                        <input type="checkbox" /><label htmlFor="">Dieta</label>
                    </div> 
                   <div>
                        <input type="checkbox" /><label htmlFor="">Dieta</label>
                    </div>
                    <div>
                        <input type="checkbox" /><label htmlFor="">Dieta</label>
                    </div>
                    <div>
                        <input type="checkbox" /><label htmlFor="">Dieta</label>
                    </div>
                    <div>
                        <input type="checkbox" /><label htmlFor="">Dieta</label>
                    </div>
                </ul>

                <button id={style.sendButton} type="submit" value="Save">Enviar</button>
            </form>
        </div>
    )
};


export default CreatePage;